import Vue from 'vue'
import Router from 'vue-router'
import index from '@/scripts/components/index'
import classfiy from '@/scripts/components/classfiy'
import show from '@/scripts/components/show'
import buycar from '@/scripts/components/buycar'
import my from '@/scripts/components/my'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index,
      redirect:'show',
      children: [
                   {
				      path: '/classfiy',
				      name: 'classfiy',
				      component: classfiy
		    		},
		    		{
				      path: '/show',
				      name: 'show',
				      component: show
		    		},
            {
              path: '/my',
              name: 'my',
              component: my
            },
            {
              path: '/buycar',
              name: 'buycar',
              component: buycar
            }
     ]
    }

  ]
})



