<template lang="html">
	<div class="show">
		<mt-swipe :auto="4000" class="z-sw" >
			  <mt-swipe-item v-for="(item,index) in dataSource" v-bind:key="index">
			  	<img  :src="item.picUrl"/>
			  </mt-swipe-item>
			 
		</mt-swipe>
	</div>
</template>
<script>
	import Vue from 'vue';
	import 'mint-ui/lib/style.css'
	import { Swipe, SwipeItem } from 'mint-ui';

	Vue.component(Swipe.name, Swipe);
	Vue.component(SwipeItem.name, SwipeItem);
	import axios from 'axios'
	export default {
	        data(){
	        	return {dataSource:[]}
	        },
			mounted:function(){
			let that=this
				axios({
					url:'http://localhost:3000/swiper',
					method:'get'
				}).then(function(res){
				   console.log(res.data.data.carousel)
				   that.dataSource=that.dataSource.concat(res.data.data.carousel)
				})
			}
  	}
</script>


